import React from "react";
import ReactDOM from "react-dom";
import bridge from "@vkontakte/vk-bridge";
import { View, Panel, PanelHeader, Div, Button, PanelHeaderBack, Cell, Avatar, ScreenSpinner, Header, Textarea } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
bridge.send("VKWebAppInit");



var activePanel = "main";
var cell = <ScreenSpinner size="medium" style={{ marginTop: 160 }}/>;


class App extends React.Component {
render() {
(async function(){
var userId = new URLSearchParams(window.location.search).get("vk_user_id");
var urlVkGet = "https://cors-anywhere.herokuapp.com/https://api.vk.com/method/users.get?user_id=" + userId + "&v=5.107&fields=photo_100&access_token=9d0a4679de9f92b8544f85b68de8bec1d34ff98994f27108ac9183c52997d5a67d9430ca710daacb5c539";
var vkGet = await fetch(urlVkGet, {method: "GET"});
var vkGet = await vkGet.json();
var userLink = vkGet.response["0"].photo_100;
var userName = vkGet.response["0"].first_name + " " + vkGet.response["0"].last_name;
cell = <Div><Cell before={<Avatar src={userLink} />} description="Вы">{userName}</Cell><Textarea style={{ marginTop: 20 }}/></Div>
ReactDOM.render(<App />, document.getElementById("root"));
}())
  return (
    <View activePanel={activePanel}>
      <Panel id="main">
        <PanelHeader>{"Меню"}</PanelHeader>
{cell}
      </Panel>
    </View>
  );
}

}

ReactDOM.render(<App />, document.getElementById("root"));
